handleAm(item, index) {
let list = this.selectAm
let cell = this.$refs.am.getElementsByTagName('li')[index]
console.log(cell)
let flag = false
for (let i = 0; i < list.length; i++) {
if (list[i].tag === item.tag) {
list.splice(i, 1)
removeClass(cell, 'active')
flag = true
}
}
if (!flag) {
list.splice(list.length, 0, item)
addClass(cell, 'active')
}
this.selectAm = list
},
